body {
    margin: 0;
    padding: 0;
    font-size: 1em;
    font-family: sans-serif;
    color: #001a34;
    height: 100vh;
}

.app {
    display: flex;
    flex-direction: column;
    min-height: 50%;
    justify-content: center;
    align-items: center;

}

.button {
    border: none;
    padding: 10px;
    background-color: #005bff;
    color: #fff;
    border-radius: 6px;
    cursor: pointer;
    margin-bottom: 10px;
}

.button:hover {
    background-color: #003ead;
}

.demo {
    padding: 10px 50px;
    color: #3d5165;
    font-size: 12px;
    line-height: 2;
}

footer {
    display: flex;
    justify-content: center;
    padding: 20px;
}

a {
    font-size: 16px;
    margin-left: 20px;
    color: #fff;
}
